import React, { useContext, useState} from 'react';
import { useHistory,useParams } from 'react-router-dom';
import { Form } from "../components";
import { HeaderContainer } from '../containers/header';
import { FooterContainer } from '../containers/footer';
import { FirebaseContext } from '../context/firebase';
import * as ROUTES from '../constants/routes';
import { useForm } from "react-hook-form";
import {
  signinRequest,
  signinSuccess,
  signinFail,
  signinPasswordError
} from '../reducers/slices/userLoginSlice';
import { useDispatch, useSelector } from 'react-redux';
import {auth} from '../lib/firebase.prod';
import { Input } from '../components/form/styles/form'; 
import { ImSpinner, ImSpinner2 } from 'react-icons/im';
 
 

export default function  SignIn() {
  //     
  const history = useHistory();

  const dispatch = useDispatch();
  const { loading, error } = useSelector((state) => state.userLogin);

  const { email } = useParams();
 

  const {
    register,
    handleSubmit,
    formState: {errors},
    watch,
    reset,
  } = useForm();


  const handleSubmitonClick = async(data) => {
      dispatch(signinRequest());
      try {
        const response = await auth.signInWithEmailAndPassword(
              data.email,
              data.password
        );
 
        dispatch(signinSuccess(JSON.stringify(response.user)));
          history.push(ROUTES.BROWSE);
      } catch(error) {
            dispatch(signinFail(error.message));
    
      }
  }; 

    //   
    return (
       <>
         <HeaderContainer>
            <Form login>
              <Form.Title></Form.Title>
              <Form.Base onSubmit={handleSubmit(handleSubmitonClick)}>  
               {error && <Form.Error>.        .     .</Form.Error>}
              {errors.wrongPassword && <Form.Error>.        .     .</Form.Error>}
 
                 <Input 
                    type="text"
                    {...register('email', {
                      required: '',

                      //    
                      pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                        message: '    ',
                      }
                    })}
                    id='email'
                    defaultValue={email ? email: ''}
                    placeholder=' '
                />
                {/*     */}
                {errors.email && <span>{errors.email.message}</span>}

               {/*     4~10      */}
                <Input 
                  type="password"
                   {...register('password', {
                     required: 
                       '  4~10   ..',
                      
                       minLength: {
                        value: 4,
                        message:
                            '  4-10   .',
                    },
                    //    15    
                    maxLength: {
                        value: 15,
                        message:
                            '    .',
                    },
                   //  
                    wrongPassword: {
                        value:'password',
                        message: '  .'
                    }
                   })}
                   id='password'
                   placeholder=""
                />
                 {errors.password && <span>{errors.password.message}</span>}
            
              {loading ? (
                <Form.Submit>
                    <ImSpinner/>
                  </Form.Submit>
              ): (
                <Form.Submit 
                type="submit" 
                data-testid="sign-in" >
                    
                </Form.Submit>
           )}
              </Form.Base>
              <Form.Text>
                Netflix  ? <Form.Link to="/signup"> </Form.Link>
              </Form.Text>
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </HeaderContainer>
         <FooterContainer signin />
       </>  
    )
}