
import React from 'react';
import { useHistory } from 'react-router-dom';
import { Form } from "../components";
import { HeaderContainer } from '../containers/header';
import { FooterContainer } from '../containers/footer';
import * as ROUTES from '../constants/routes';
import {useForm} from 'react-hook-form'
 import {
     registerRequest,
     registerSuccess,
     registerFail,
 } from '../reducers/slices/userRegisterSlice';
import { useDispatch, useSelector } from 'react-redux';
// import auth from 'registry-auth-token'
import {auth} from '../lib/firebase.prod';
import { Input } from '../components/form/styles/form';
import {ImSpinner} from "react-icons/im";


export default function  SignUp() {
   const history = useHistory();
 
   const dispatch = useDispatch();
   const { loading, error } = useSelector((state) => state.userRegister);
   const { user } = useSelector((state) => state.userLogin);

   const {
      register,
      handleSubmit,
      formState: {errors},
      watch,
      reset
   } = useForm();


 

    const handleSubmitonClick = async(data) => {
      dispatch(registerRequest());
      try {
         const response = await auth.createUserWithEmailAndPassword(
            data.email,
            data.password
        );
         await response.user.updateProfile({
            displayName:data.email,
            photoURL: Math.floor(Math.random() * 5) + 1,
         });
         dispatch(registerSuccess(JSON.stringify(response.user)));
         history.push(ROUTES.BROWSE);
         } catch (error) { 
            dispatch(registerFail(error.message));     
          }
      };
    
    

    //   
    return (
       <>
         <HeaderContainer>
            <Form login>
              <Form.Title> </Form.Title>
              {/*    */}
              <Form.Base onSubmit={handleSubmit(handleSubmitonClick)}>
                 {/*      */}
              {error && <Form.Error>.        .</Form.Error>}
                 <Input
                           type='text'
                           {...register('email', {
                               required: '',

                               //     
                               pattern: {
                                   value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                                   message: '    ',
                               },
                           })}
                            id='email'
                            placeholder=' '
                    />
                    {/*     */}
                     {errors.email && <span>{errors.email.message}</span>}

                     {/*     4~10      */}
                   
                   <Input
                           type='password'
                           {...register('password', {
                               required:
                                   '  4~10   ..',

                              //    minLength 4     
                               minLength: {
                                   value: 4,
                                   message:
                                       '  4-10   .',
                               },
                               //    15    
                               maxLength: {
                                   value: 15,
                                   message:
                                       '    .',
                               },
                           })}
                           id='password'
                           placeholder=''
                    />
                    {errors.password && <span>{errors.password.message}</span>}
                {loading ? ( 
                   <Form.Submit>
                        <ImSpinner/>
                     </Form.Submit>
            ): (
               <Form.Submit 
                   type="submit"   
                   data-testid="sign-up">
                   
                </Form.Submit>
            )}
      

               
              </Form.Base>
              <Form.Text>
                Netflix  ? <Form.Link to={ROUTES.SIGN_UP}> </Form.Link>
              </Form.Text>
              <Form.Text>
                    Google reCAPTCHA      . <Form.AboutLink href=""> </Form.AboutLink>
              </Form.Text>
            </Form>
         </HeaderContainer>
            <FooterContainer/>
       </>  
    )
}

 